@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }
html {
  font-size: 120%;
}
body {
  font-family: 'Muli', sans-serif;
  background: #fafafa;
}

img {
  display: block;
  width: 100%;
}


.post {
    background: white;
    /* border: 1px solid #F83E40; */
    padding: 20px;
    margin-bottom: 40px;
    border-radius: 20px;
    box-shadow: 0px 5px 20px -8px rgba(0,0,0,0.25);
}

.post img {
    border-radius: 10px 10px 0 0;
}
header {
    background: #F83E40;
    color: white;
}

header img {
    width: 200px;
}

h1 {
    text-align: center;
    margin: 0;
    padding: 40px;
    text-transform: uppercase;
}

.post-holder,
.container {
    margin: 0 auto;
    max-width: 800px;
    padding: 20px 5%;
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.container .log-out {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container p {
    background: white;
    color: #F83E40;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    margin: 0;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.container p:hover {
    color: #f9f9f9;
    background: #F83E40;
    border: 1px solid white;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.error-message {
    background: red;
    color: white;
    text-align: center;
    padding: 20px;
    border-radius: 5px;
}
.modal {
    color: #F83E40;
}

.modal h3 {
    text-align: center;
}

.modal button {
    display: block;
    margin: 0 auto;
    width: 50%;
    background: #F83E40;
    color: white;
    border: 0px solid;
    padding: 10px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.modal button:hover {
    color: #F83E40;
    background: white;
    border: 2px #F83E40 solid;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
.login {
    max-width: 500px;
    margin: 100px auto 0 auto;
}

.logo {
    width: 150px;
    margin: 0 auto 50px auto;
}

.inputs {
    box-shadow: 0px 10px 20px -20px rgba(0,0,0,0.75);
}

.inputs .input {
    padding: 0 0 0 20px;
    border: 1px solid #d1d1d1;
    border-radius: 10px 10px 0 0;
    background: white;
}

.inputs .input:last-of-type {
    border-top: 0px;
    border-radius: 0 0 10px 10px;
}

.inputs input {
    padding: 20px 0;
    width: calc(100% - 100px);
    border: 0px solid white;
    border-radius: 0px 10px 0 0;
    padding-left: 10px;
}

.inputs .input:last-of-type input{
    border-radius: 0 0 10px 0;
}

.inputs label {
    display: inline-block;
    width: 100px;
    color: #999;
}
.submit-button {
    display: block;
    width: 250px;
    background: #F83E40;
    color: white;
    margin: 0 auto;
    border: 0px;
    padding: 10px;
    border-radius: 10px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.auxilary-buttons {
    display: flex;
    flex-direction: row;
    padding: 20px 0;
}

.auxilary-buttons label {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: auto;
    padding-left: 10px;
}

.auxilary-buttons input[type="checkbox"] {
    border: 1px solid #F83E40;
}

.auxilary-buttons p:hover {
    color: #F83E40;
    cursor: pointer;
    text-decoration: underline;
    /* color: white; */
}

.checkbox {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

input:focus {
    background: #F83E40;
    color: white;
}

.submit-button:hover {
    background: #f9f9f9;
    color: #F83E40;
    border: 1px solid #F83E40;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    box-shadow: 0px 10px 20px -20px rgba(0,0,0,0.75);
}

@media screen and (max-width: 520px) {
    html {
        font-size: 100%;
    }
    .login {
        margin: 80px 2.5% 0 2.5%;
    }
  }

